@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
html, body {
  width: 100%;
  margin: 0;
  padding: 0;
  background: #1f3a4a;
}
.container {
  position: relative;
  left: 25%;
  width: 50%;
  height: 1000px;
  font-family: 'Press Start 2P';
  top: 30px;
}
.container .header {
    position: relative;
    width: 100%;
    height: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.container .header .header-title {
      position: absolute;
      width: 75%;
}
.container .header .header-title img {
        position: aboslute;
        width: 90%;
        padding-left: 10%;
}
.container .header .header-img {
      position: absolute;
      width: 30%;
      right: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.container .header .header-img img {
        position: absolute;
        width: 40%;
}
.container .button-list {
    position: relative;
    width: 100%;
    height: 80px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    color: #c9ccbe;
}
.container .button-list .list-item {
      width: 110px;
      height: 33px;
      border: 1.3px solid #cbb8b1;
      margin: 0px 10px;
      font-size: .8em;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-transition: .3s;
      transition: .3s;
}
.container .button-list .list-item:hover {
        cursor: pointer;
}
.container .button-list .list-item .circle {
        height: 10px;
        width: 10px;
        margin-right: 5px;
        border-radius: 50%;
        border: 2px solid #213849;
        background-color: #bf939a;
}
.container .online-content {
    margin-bottom: 10px;
}
.container .online-content .online-li {
      border: 1px solid #c9b6af;
      background: #1d3848;
}
.container .offline-content .offline-li {
    background: #493a4d;
}
.container .content-list {
    position: relative;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.container .content-list .content-list-li {
      position: relative;
      width: 90%;
      height: 110px;
      margin: 10px 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.container .content-list .content-list-li .li-photo {
        position: absolute;
        width: 30%;
        height: 100%;
        left: 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.container .content-list .content-list-li .li-photo .photo-img {
          position: absolute;
          width: 50px;
          height: 50px;
          border-radius: 50%;
          border: 1.5px solid;
          border-color: #c9bcb2;
          overflow: hidden;
}
.container .content-list .content-list-li .li-photo .photo-img img {
            position: absolute;
            width: 100%;
}
.container .content-list .content-list-li .li-text {
        position: absolute;
        width: 70%;
        height: 100%;
        right: 0;
        font-size: .8em;
}
.container .content-list .content-list-li .li-text .text {
          position: relative;
          width: 90%;
          height: 22px;
          margin-top: 10px;
          left: 5%;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
}
.container .content-list .content-list-li .li-text .text a {
            text-decoration: none;
            color: #c1969d;
}
.container .content-list .content-list-li .li-text .text-id {
          color: #c1969d;
}
.container .content-list .content-list-li .li-text .text-status {
          color: #c1969d;
          font-family: 'Noto Sans TC', sans-serif;
}
.container .content-list .content-list-li .li-text .text-game {
          color: #c9ccbe;
}
.container .content-list .content-list-li .li-text .text-offline {
          color: #b56d80;
}
@media (max-width: 800px) {
.container {
    width: 70%;
    left: 15%;
}
}
@media (max-width: 720px) {
.container {
    width: 80%;
    left: 10%;
}
}
@media (max-width: 550px) {
.container {
    width: 100%;
    left: 0;
    top: 10px;
}
.circle {
    display: none;
}
}
